module.exports["delivery"] = Handlebars.template({"compiler":[7,">= 4.0.0"],"main":function(container,depth0,helpers,partials,data) {
    return "<div class=\"modal delivery\">\n    <h2 class=\"delivery__title\">Оплата и доставка</h2>\n    <div class=\"delivery__cols\">\n        <div class=\"delivery__col column3\">\n            <h3>Доставка по Киеву</h3>\n            <p>Наша служба доставки работает для Вас с понедельника по пятницу, с 14:00 до 19:00.</p>\n            <p>Доставка товаров в день заказа возможна, при оформлении заказа до 12:00 и при условии наличия товара на нашем складе.</p>\n            <p>Во время заказа наши менеджеры сразу согласуют с вами удобное время доставки.\n                За 1 час до доставки заказа курьер обязательно свяжется с Вами для подтверждения времени и места доставки.</p>\n        </div>\n        <div class=\"delivery__col column6\">\n            <h3>Доставка по Украине</h3>\n            <p>Доставка по Украине осуществляется с помощью перевозчиков Новая почта, Автолюкс и УкрПочта, либо курьером по Украине.</p>\n            <h4>Новая почта</h4>\n            <p>Заказы доставляются на склад компании Новая Почта в Вашем городе.</p>\n            <p>Стоимость доставки зависит от суммы покупки:\n                <br>\n                <br>\n                до 300 грн - 20 грн\n                <br>\n                свыше 300 грн - доставка бесплатная.</p>\n            <p>Оплатить товар Вы можете на складе в момент получения заказа (дополнительно Вы оплачиваете 2% от суммы заказа + 17-18 грн (в зависимости от удаленности от Киева) за услугу обратной отправки денежных средств через Новую Почту).</p>\n            <h4>Автолюкс</h4>\n            <p>Заказы доставляются на склад компании Автолюкс в Вашем городе.</p>\n            <p>Стоимость доставки зависит от суммы покупки:\n                <br>\n                <br>\n                до 300 грн - 20 грн\n                <br>\n                свыше 300 грн - доставка бесплатная.</p>\n            <p>Адрес ближайшего представительства можно узнать <a href=\"#\">здесь</a>.</p>\n        </div>\n        <div class=\"delivery__col column3\">\n            <h3>Оплата заказа</h3>\n            <h4>Наличная</h4>\n            <p>Заказ оплачивается наличными курьеру в момент доставки либо на складе компании-перевозчика при получении. Оплата производится только в национальной валюте. В подтверждение факта оплаты мы выдаем товарный чек.</p>\n            <h4>Безналичная</h4>\n            <p>При выборе безналичного способа оплаты наш менеджер отправит Вам счет-фактуру, который Вы сможете оплатить в любом банковском отделении или с расчетного счета Вашей компании.</p>\n        </div>\n    </div>\n</div>";
},"useData":true});
